<template>
    <div class="tab" js-tab="1">
        <homeTab></homeTab>
        <div class="tabzhanghao">
            <div id="tab1" class="tab1">
                <a to="javascript:void(0)" v-on:click="showTab('1')">消费记录</a>
            </div>
            <div id="tab2" class="tab2">
                <a to="javascript:void(0)" v-on:click="showTab('2')">充值记录</a>
            </div>
        </div>
        <div class="tab-cont">
            <ul class="tab-cont__wrap">
                <!--客户管理-->
                <li class="item" id="cont1" style="display:block;">
                    <div class="box">
                        <div class="box-bor">
                            <table class="table">
                                <caption style="text-align:center;font-size:26px;">我的消费记录</caption>
                                <thead>
                                    <tr>
                                        <th style="width:6%;">流水号</th>
                                        <th style="width:10%;">客户</th>
                                        <th style="width:10%;">平台类型</th>
                                        <th style="width:10%;">合作平台</th>
                                        <th style="width:10%;">交易类型</th>
                                        <th style="width:10:%;">交易金额</th>
                                        <th style="width:10%;">可用余额</th>
                                        <th style="width:12%;">交易日期</th>
                                        <th style="width:6%;">充值状态</th>
                                        <th style="width:16%;">备注</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr class="active">
                                        <td>{{running_num}}</td>
                                        <td>{{realname}}</td>
                                        <td>小程序</td>
                                        <td>{{appmcheng}}</td>
                                        <td>{{deal_type}}</td>
                                        <td>{{deal_gold}}</td>
                                        <td>{{balance}}</td>
                                        <td>{{time1 | formatDate}}</td>
                                        <td>{{state}}</td>
                                        <td>{{appmcheng}}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </li>
                <!--添加用户-->
                <li class="item" id="cont2" style="display:none;">
                    <div class="box">
                        <div class="box-bor">
                            <table class="table">
                                <caption style="text-align:center;font-size:26px;">我的充值记录</caption>
                                <thead>
                                    <tr>
                                        <th style="width:5%;">序号</th>
                                        <th style="width:10%;">充值账号</th>
                                        <th style="width:8%;">充值金额</th>
                                        <th style="width:8%;">充值日期</th>
                                        <th style="width:10%;">是否充值成功</th>
                                        <th style="width:6%;">备注</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr class="active">
                                        <td>1</td>
                                        <td>深圳有限公司</td>
                                        <td>{{recharge_gold}}</td>
                                        <td>{{time | formatDate}}</td>
                                        <td>{{state}}</td>
                                        <td>{{company}}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </li>
            </ul>
            <div class="bottom">
                <p>版权所有：2017深恒利信息技术有限公司</p>
            </div>
        </div>
    </div>
</template>
<script>
import homeTab from '@/components/Tab'
import { formatDate } from '../../assets/js/date'
export default {
    name: 'nas_wallet',
    data() {
        return {
            recharge_gold: '',
            recharge_time: '',
            user_company: '',
            content: '',
            company: '',
            state: '',

            running_num: '',
            deal_type: '',
            deal_gold: '',
            balance: '',
            dj_balance: '',
            deal_time: '',
            state: '',
            appmcheng: '',
            realname: '',

        }
    },
    //组件构建
    components: {
        homeTab
    },
    mounted() {
        this.init(),
            this.init2()
    },
    methods: {
        showTab: function(id) {
            var a = function(id) {
                return document.getElementById(id);
            }
            for (var i = 1; i < 3; i++) {
                if (id == i) {
                    a("cont" + i).style.display = "block";
                } else {
                    a("cont" + i).style.display = "none";
                }
            }
            return false;
        },
        init() {
            this.$http.get('http://123.207.41.93/jtct/index.php/Agency/recharge/recharge_list?id=1', {
                emulateJSON: true
            }).then(function(res) {
                //alert("成功：" + res.body)
                //console.log("成功：" + JSON.stringify(res.body))
                this.recharge_gold = res.body[0].recharge_gold
                this.recharge_time = res.body[0].recharge_time
                this.user_company = res.body[0].company
                this.company = res.body[0].company
                this.state = res.body[0].state
                // this.validity = JSON.stringify(res.body[0].start_time)
            }, function(err) {
                //  alert("失败：" + err.body)
            })
        },
        init2() {
            this.$http.get('http://123.207.41.93/jtct/index.php/Agency/deal/deal_list?id=1', {
                emulateJSON: true
            }).then(function(res) {
                //alert("成功：" + res.body)
                console.log("成功：" + JSON.stringify(res.body))
                this.running_num = res.body[0].running_num
                this.deal_type = res.body[0].deal_type
                this.deal_gold = res.body[0].deal_gold
                this.balance = res.body[0].balance
                this.dj_balance = res.body[0].dj_balance
                this.deal_time = res.body[0].deal_time
                this.state = res.body[0].state
                this.appmcheng = res.body[0].appmcheng
                this.realname = res.body[0].realname
                // this.validity = JSON.stringify(res.body[0].start_time)
            }, function(err) {
                //  alert("失败：" + err.body)
            })
        },
    },
    filters: {
        formatDate(time) {
            var data = data;
            return formatDate(data, 'yyyy-MM-dd hh:mm');
        },
        formatDate(time1) {
            var data1 = data1;
            return formatDate(data1, 'yyyy-MM-dd hh:mm');
        }
    }
}
</script>
<style>
.tab_title {
    margin: 60px;
    padding: 20px
}
</style>



