<template>
    <div id="order">
        <homeTab></homeTab>
        <div class="alert alert-danger">
            溫馨提示：尊敬的服务商：
            <span>“深圳市有限公司”</span>
            您好！ 您当前账户的可用余额为：
            <span>{{balance}}元</span>
        </div>
        <div class="center-button">
            <button class="btn btn-success">
                <a class="aa" href="append/append.html">+添加小程序</a>
            </button>
        </div>
        <div class="contert">
            <p>
                <span id="cov" style="margin:40px;">服务商:{{facilitator}}</span>
                <span id="cov" style="margin:40px;">客户:{{client}}</span>
                <span id="cov" style="margin:40px;">手机号:{{cellphone}}</span>
            </p>
            <div class="bnt">
                小程序名：
                <span>{{applet}}</span>
            </div>
            <div class="bon">

                <router-link to="/contract">合同信息</router-link>
                <router-link to="/edit">参数编辑</router-link>
            </div>
            <div id="div3">
                服务有效期:
                <span>{{time | formatDate}} - {{time1 | formatDate}}</span>
                <router-link to="/">管理小程序</router-link>
            </div>
        </div>
    </div>
</template>
<script>
import homeTab from '../../components/Tab'
import {formatDate} from '../../assets/js/date'
var a = ''
var data = ''
var data2 = ''
$(document).ready(function() {
    var url = location.search;
    var Request = new Object();
    if (url.indexOf("?") != -1) {
        var str = url.substr(1)　 //去掉?号
        strs = str.split("&");
        for (var i = 0; i < strs.length; i++) {
            Request[strs[i].split("=")[0]] = unescape(strs[i].split("=")[1]);
        }
    }
    a = Request["username"];
});
export default {
    name: 'order',
    data() {
        return{
            balance:'666',
            facilitator:'吉泰创投有限公司',
            client:'嗷嗷嗷',
            cellphone:'13977284441',
            applet:'大天猫商城',
        }
    },
    components: {
        homeTab
    },
    //页面加载时请求API数据渲染页面
created() {
    this.$nextTick(function () {
            this.$http.get('http://123.207.41.93/jtct/index.php/Agency/index/index?id=1', {
            emulateJSON: true
        }).then(function(res) {
           // alert("成功：" + res.body)
            console.log("成功：" + JSON.stringify(res.body[0]))
             this.balance = JSON.stringify(res.body[0].name)
             this.facilitator = JSON.stringify(res.body[0].tel)
             this.client = JSON.stringify(res.body[0].name     )   
             this.cellphone = JSON.stringify(res.body[0].tel)
             this.applet = JSON.stringify(res.body[0].appmcheng )
            // this.validity = JSON.stringify(res.body[0].start_time)
             data = JSON.stringify(res.body[0].start_time)
             data2 = JSON.stringify(res.body[0].end_time)
        }, function(err) {
          //  alert("失败：" + err.body)
        })
     })
  },
      filters:{
        formatDate(time) {
            var data = data;
            return formatDate(data, 'yyyy-MM-dd hh:mm');
        },
        formatDate(time1) {
            var data2 = data2;
            return formatDate(data2, 'yyyy-MM-dd hh:mm');
        }
    },
}
</script>
<style>
/*溫馨提示*/

.alert-danger {
    margin: 20px 20px;
}


/*添加小程序*/
.center-button {
    float: right;
    height: 60px;
    margin: 40px;
    padding-left: 30px;
}
.aa{
    padding: 30px;
    color: white;
}

/*表單*/

.contert {
    margin-top: 130px;
    width: auto;
    height: 180px;
    border: 1px solid #333333;
    border-radius: 8px;
    font-size: 18px;
    background-color: gainsboro;
}

.contert #cov {
    margin-left: 20px;
}

.contert p {
    height: 40px;
    border-bottom: 1px solid #333333;
    line-height: 40px;
    font-size: 16px;
    padding-left: 20px;
}

.contert .bnt {
    float: left;
    height: 80px;
    margin-left: 40px;
    line-height: 60px;
}

.contert .bon {
    float: right;
    margin-right: 40px;
    line-height: 60px;
}

.contert .bon a {
    border: 1px solid #333333;
    padding: 10px 20px;
    border-radius: 6px;
    margin: 2px;
    background-color: peachpuff;
}

.contert #div3 {
    border-top: 1px solid #333333;
    clear: both;
    padding-left: 20px;
    line-height: 50px;
}

.contert #div3 a {
    float: right;
    margin-right: 40px;
}
</style>




