<template>
  <div class="tab" js-tab="1">
    <homeTab></homeTab>
    <div class="tabzhanghao">
      <div id="tab1" class="tab1">
        <a to="javascript:void(0)" v-on:click="showTab('1')">客户管理</a>
      </div>
      <div id="tab2" class="tab2">
        <a to="javascript:void(0)" v-on:click="showTab('2')">添加用户</a>
      </div>
    </div>
    <div class="tab-cont">
      <ul class="tab-cont__wrap">
        <!--客户管理-->
        <li class="item" id="cont1" style="display:block;">
          <div class="box">
            <div class="box-bor">
              <div class="panel panel-info">
                <div class="panel-heading">快速查询</div>
                <div class="panel-body">
                  <form class="form-horizontal" name="form_sousuo" id="form_sousuo">
                    <p>服务商 <input type="text" name="name" id="name" readonly="readonly" class="form-control" v-model="name">
                    </p>
                    <div class="form-group">
                      <p>关键词<input class="form-control" v-model="str" type="text" value="" placeholder="请输入客户姓名/平台名称"></p>
                      <div class="col-xs-12 col-sm-2 col-lg-2">
                        <input class="btn btn-default" type="button" v-on:click="submit_sousuo" value="搜索"></input>
                      </div>
                    </div>
                  </form>
                </div>
              </div>
              <table class="table">
                <thead>
                  <tr>
                    <th style="width:8%;">序号</th>
                    <th style="width:12%;">服务商</th>
                    <th style="width:12%;">客户</th>
                    <th style="width:12%;">小程序</th>
                    <th style="width:8%;">快捷通知</th>
                    <th style="width:8%;">邮箱通知</th>
                    <th style="width:8%;">状态</th>
                    <th style="width:18%;">操作</th>
                  </tr>
                </thead>
                <tbody>
                  <tr class="active">
                    <td>{{index}}</td>
                    <td>{{company}}</td>
                    <td>{{realname}}</td>
                    <td>
                      <router-link to="/client">{{appmcheng}}</router-link>
                    </td>
                    <td>{{no_status}}</td>
                    <td>{{yes_status}}</td>
                    <td>{{state}}</td>
                    <td>
                      <router-link to="/client">编辑</router-link>|
                      <router-link to="">小程序</router-link>|
                      <router-link to="">禁止</router-link>|
                      <router-link to="/Recharge">充值</router-link>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </li>
        <!--添加用户-->
        <li class="item" id="cont2" style="display:none;">
          <div class="box">
            <form class="form-horizontal ajaxfrom" id="add_user" name="add_user" method="post" enctype="multipart/form-data" action="http://123.207.41.93/jtct/index.php/Agency/user/add_user">
              <div class="form-group">
                <label class="col-xs-12 col-sm-2 col-md-2 col-lg-2 control-label">用户名</label>
                <div class="col-sm-10 col-lg-9 col-xs-12">
                  <input v-model="name" type="text" class="form-control" value="">
                  <span class="help-block" style="color:palevioletred;">请您输入用户名，用户名为 3 到 15 个字符组成，包括汉字，大小写字母（不区分大小写）</span>
                </div>
              </div>
              <div class="form-group">
                <label class="col-xs-12 col-sm-2 col-md-2 col-lg-2 control-label">密码</label>
                <div class="col-sm-10 col-lg-9 col-xs-12">
                  <input v-model="psw" type="password" class="form-control" autocomplete="off">
                  <span class="help-block" style="color:palevioletred;">请您输入密码，最小长度为 8 个字符</span>
                </div>
              </div>
              <div class="form-group">
                <label class="col-xs-12 col-sm-2 col-md-2 col-lg-2 control-label">确认密码</label>
                <div class="col-sm-10 col-lg-9 col-xs-12">
                  <input v-model="psw1" type="password" class="form-control" autocomplete="off">
                  <span class="help-block" style="color:palevioletred;">请您再次输入密码，确认密码正确输入</span>
                </div>
              </div>

              <div class="form-group">
                <label class="col-xs-12 col-sm-2 col-md-2 col-lg-2 control-label">客户姓名</label>
                <div class="col-sm-10 col-lg-9 col-xs-12">
                  <input v-model="realname" required="" type="text" class="form-control" value="" autocomplete="off">
                </div>
              </div>
              <div class="form-group">
                <label class="col-xs-12 col-sm-2 col-md-2 col-lg-2 control-label">身份证号</label>
                <div class="col-sm-10 col-lg-9 col-xs-12">
                  <input v-model="idcard" required="" type="text" class="form-control" value="" autocomplete="off">
                </div>
              </div>
              <div class="form-group">
                <label class="col-xs-12 col-sm-2 col-md-2 col-lg-2 control-label">手机号</label>
                <div class="col-sm-10 col-lg-9 col-xs-12">
                  <input v-model="tel" required="" type="text" class="form-control" value="" autocomplete="off">
                </div>
              </div>
              <div class="form-group">
                <label class="col-xs-12 col-sm-2 col-md-2 col-lg-2 control-label">邮箱</label>
                <div class="col-sm-10 col-lg-9 col-xs-12">
                  <input v-model="mail" type="email" required="" class="form-control" value="" autocomplete="off">
                </div>
              </div>
              <div class="form-group">
                <label class="col-xs-12 col-sm-2 col-md-2 col-lg-2 control-label">身份证正面</label>
                <div class="col-sm-10 col-lg-9 col-xs-12">
                  <div class="input-group ">
                    <!-- <input id="idcard1" name="idcard" type="file" /> -->
                    <input type="file" name="idcard1"   @change="changeImage($event)" ref="avatarInput">
                    <div class="image_container">
                      <img id="preview1" width="140" height="90">
                    </div>
                  </div>
                </div>
              </div>
              <div class="form-group">
                <label class="col-xs-12 col-sm-2 col-md-2 col-lg-2 control-label">身份证反面</label>
                <div class="col-sm-10 col-lg-9 col-xs-12">
                  <div class="input-group ">
                    <input  name="idcard" type="file" />
                    <div class="image_container">
                      <img id="preview2" width="140" height="90">
                    </div>
                  </div>
                </div>
              </div>
              <div class="form-group">
                <div style="text-align:center;">
                  <input type="submit" @click="submil_zhuce" class="btn btn-primary span3" id="add_btn" value="确认注册">
                </div>
              </div>
            </form>
          </div>
        </li>4012
      </ul>
      <div class="bottom">
        <p>版权所有：2017深恒利信息技术有限公司</p>
      </div>
    </div>
  </div>
</template>
<script src="https://cdn.bootcss.com/jquery/2.1.4/jquery.min.js"></script>
<script>
import homeTab from '@/components/Tab'
//  console.log("username::::" + homeTab._username)

$("#add_btn").click(function() {
  $.ajax({
    url: "http://123.207.41.93/jtct/index.php/Agency/user/add_user",
    type: "POST",
    data: $('#add_user').serialize(),
    success: function(data) {
      console.log("aaaaaa" + JSON.stringify(data))
    }
  });
});
export default {
  name: 'nas_number',
  data() {
    return {
      name: '',
      str: '',
      index: '1',
      company: homeTab._username,
      realname: '',
      appmcheng: '',
      no_status: '无',
      yes_status: '未绑定',
      state: '',
    }
  },
  //组件构建
  components: {
    homeTab
  },
  created() {
    this.$nextTick(function() {
      this.$http.get('http://123.207.41.93/jtct/index.php/Agency/user/user_list?id=1', {
        emulateJSON: true
      }).then(function(res) {
        //alert("成功：" + res)
        console.log("aaa" + JSON.stringify(res.body))
        //this.balance = res.body.data.company)
        this.realname = res.body[0].realname
        this.tel = res.body[0].tel
        this.appmcheng = res.body[0].appmcheng
        this.state = res.body[0].state

      }, function(err) {
        //  alert("失败：" + err.body)
      })
    })
    this.name = homeTab._username;
  },
  methods: {
    showTab: function(id) {
      var a = function(id) {
        return document.getElementById(id);
      }
      for (var i = 1; i < 3; i++) {
        if (id == i) {
          a("cont" + i).style.display = "block";
        } else {
          a("cont" + i).style.display = "none";
        }
      }
      return false;
    },
    submit_sousuo: function() {
      let data = { 'id': 1, 'str': this.str }
      this.$http.post('http://123.207.41.93/jtct/index.php/Agency/user/search', data, {
        emulateJSON: true
      }).then(function(res) {
        // alert(res.body.msg)
        alert(JSON.stringify(res.body))
        if (res.body.status == 1) {
          //提交后清空
          this.$data = Object.assign(this.$data, this.defaultData);
          alert(res.body.msg)
        }
      }, function(err) {
        alert(err.body.msg)
      })
    },
    submil_zhuce: function() {
      let data = { 'name': homeTab._username, 'psw ': this.psw,'psw1':this.psw1
      ,'mail':this.mail,'realname':this.realname,'idcard':this.idcard
      ,'tel':this.tel,'idcard1':this.idcard1,'idcard2':this.idcard2
      ,'agency_id':1 }
      this.$http.post('http://123.207.41.93/jtct/index.php/Agency/user/add_user', data, {
        emulateJSON: true
      }).then(function(res) {
        // alert(res.body.msg)
        alert(JSON.stringify(res.body))
        if (res.body.status == 1) {
          //提交后清空
          this.$data = Object.assign(this.$data, this.defaultData);
          alert(res.body.msg)
        }
      }, function(err) {
        alert(err.body.msg)
      })
    },
  }
    // ("#add_btn").click(function(){
    // 	$.ajax({
    // 		url:"http://123.207.41.93/jtct/index.php/Agency/user/add_user",
    // 		type:"POST",
    // 		data:$('#add_user').serialize(),
    // 		success: function(data){
    // 			console.log("aaaaaa" + JSON.stringify(data))
    // 		}
    // 	});
    // });
  
}
</script>
<style>
.tab {
  width: auto;
  border-radius: 16px;
}

.tab-title {
  height: 60px;
  font-size: 20;
  background-color: #f5f5f5;
}

.tab-title .item {
  display: inline-block;
  width: 140px;
  height: 60px;
  line-height: 30px;
  text-align: center;
  color: #333;
  font-size: 16px;
}





/*选项卡*/

.tab-title .item-cur {
  color: #fff;
  background-color: #80b600;
  line-height: 60px;
}

.tab-cont {
  position: relative;
  width: 100%;
  height: auto;
  overflow: hidden;
}





/*内容*/

.tab-cont .item {
  width: 100%;
  height: 100%;
  color: #fff;
}

.item .box {
  width: 100%;
  min-height: 450px;
  padding-bottom: 20px;
  background-color: darkgray;
  border: 1px solid darkgray;
}

.box-bor {
  width: 90%;
  height: auto;
  margin: 0px auto;
  border-radius: 10px;
  padding-top: 40px;
  color: #333333;
}
</style>



